@import '~styles/index';

.example-hyper-modal-wrapper {

  .header {
    padding: 50px 0;
    width: 100%;
    display: flex;
    justify-content: center;
    .title {
      color: #313443;
      font-weight: bold;
      font-size: 3em;
      text-transform: uppercase;
      text-align: center;
    }
  }

  .content {
    padding-bottom: 40px;
  }

  .hyper-modal-wrapper {
    position: fixed;

    .hyper-modal-content-wrapper {
      @include mobile {
        width: 90%;
      }
    }

    .modal-content {
      .hyper-modal-default-content {
        padding: 30px 0;
        .title {
          color: #282c34;
        }
        .description {
          text-align: center;
        }
      }
    }
  }
}
