@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,700);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Montserrat, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif; }

html {
  font-size: 14px;
  overflow-x: hidden; }

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  height: 100vh; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

#root {
  width: 100%;
  height: 100%; }

.home-wrapper {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: #313443; }

.title {
  color: white; }

.base-layout-wrapper {
  width: 100%;
  min-height: 100%;
  display: flex;
  justify-content: center;
  background-color: #f3f3f6; }
  @media (min-width: 1024px) {
    .base-layout-wrapper {
      padding: 20px 0; } }
  .base-layout-wrapper .content-wrapper {
    width: 100%;
    box-sizing: border-box;
    background-color: #e7e8ee;
    box-shadow: 0 10px 20px rgba(49, 52, 67, 0.2), 0 6px 6px rgba(49, 52, 67, 0.3);
    padding: 0 20px; }
    @media (min-width: 768px) and (max-width: 1023px) {
      .base-layout-wrapper .content-wrapper {
        padding: 0 40px; } }
    @media (min-width: 1024px) {
      .base-layout-wrapper .content-wrapper {
        max-width: 768px;
        padding: 0 40px; } }

.custom-button {
  height: 60px;
  min-width: 200px;
  font-size: 1.2em;
  padding: 0 20px;
  border: 0;
  cursor: pointer;
  text-transform: uppercase;
  background-color: rgba(65, 70, 90, 0.5);
  box-shadow: 0px 0px 5px 1px rgba(49, 52, 67, 0.4);
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease; }
  .custom-button:hover {
    background-color: #41465a;
    color: #e7e8ee;
    box-shadow: 0px 0px 5px 1px rgba(49, 52, 67, 0.6); }

.button-wrapper {
  width: 100%;
  display: flex; }
  .button-wrapper.left {
    justify-content: flex-start; }
  .button-wrapper.center {
    justify-content: center; }
  .button-wrapper.right {
    justify-content: flex-end; }

.custom-header-text {
  color: #313443;
  padding: 20px 0; }

.example-hyper-modal-wrapper .header {
  padding: 50px 0;
  width: 100%;
  display: flex;
  justify-content: center; }
  .example-hyper-modal-wrapper .header .title {
    color: #313443;
    font-weight: bold;
    font-size: 3em;
    text-transform: uppercase;
    text-align: center; }

.example-hyper-modal-wrapper .content {
  padding-bottom: 40px; }

.example-hyper-modal-wrapper .hyper-modal-wrapper {
  position: fixed; }
  @media (max-width: 767px) {
    .example-hyper-modal-wrapper .hyper-modal-wrapper .hyper-modal-content-wrapper {
      width: 90%; } }
  .example-hyper-modal-wrapper .hyper-modal-wrapper .modal-content .hyper-modal-default-content {
    padding: 30px 0; }
    .example-hyper-modal-wrapper .hyper-modal-wrapper .modal-content .hyper-modal-default-content .title {
      color: #282c34; }
    .example-hyper-modal-wrapper .hyper-modal-wrapper .modal-content .hyper-modal-default-content .description {
      text-align: center; }

.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: #313443;
}

.title {
  color: white;
}

